
.topnav[data-v-9e110d9e] {
    background-color: #333;
    overflow: hidden;
    padding-top: 70px;
}


/* Style the links inside the navigation bar */
.topnav a[data-v-9e110d9e] {
    float: left;
    display: block;
    color: #f2f2f2;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 17px;
}


/* Change the color of links on hover */
.topnav a[data-v-9e110d9e]:hover {
    background-color: #ddd;
    color: black;
}


/* Add a color to the active/current link */
.topnav a.active[data-v-9e110d9e] {
    background-color: #4CAF50;
    color: white;
}



